* Default Theme
--theme-color: #4154f1;
--theme-color-dark: #012970; #0f3984;
--theme-counter-color: #0b198f;
*/

:root {
    scroll-behavior: smooth;
    --theme-color: #1c7ff2;
    --theme-color-hover: #0d64c8;
    --theme-color-dark: #00409a;
    --theme-counter-color: #225794;
}

::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-track {
    background: #d6e9ff;
}

::-webkit-scrollbar-thumb {
    background: #5ea9ff;
    border-radius: 20px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: var(--theme-color);
}

body {
    font-family: "Open Sans", sans-serif;
    color: #444444;
}

a {
    color: var(--theme-color);
    text-decoration: none;
}

button.btn.btn-primary {
    background: var(--theme-color);
}

a:hover {
    color: var(--theme-color);
    text-decoration: none;
}

.link {
    color: var(--theme-color);
    cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: "Open Sans", sans-serif;
}

/* Sections */
section {
    padding: 60px 0;
    overflow: hidden;
}

.section-header {
    text-align: center;
    padding-bottom: 40px;
}

.section-header h2,
#headingMutate {
    font-size: 13px;
    letter-spacing: 1px;
    font-weight: 700;
    margin: 0;
    color: var(--theme-color);
    text-transform: uppercase;
}

.section-header p {
    margin: 10px 0 0 0;
    padding: 0;
    font-size: 38px;
    line-height: 42px;
    font-weight: 700;
    color: var(--theme-color-dark);
    font-family: "Roboto", sans-serif;
}

@media (max-width: 768px) {
    .section-header p {
        font-size: 28px;
        line-height: 32px;
        cursor: auto;
    }
}

/* Breadcrumbs */
.breadcrumbs {
    padding: 15px 0;
    background: #012970;
    min-height: 40px;
    margin-top: 82px;
    color: #fff;
}

@media (max-width: 992px) {
    .breadcrumbs {
        margin-top: 57px;
    }
}

.breadcrumbs h2 {
    font-size: 28px;
    font-weight: 500;
}

.breadcrumbs ol {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    padding: 0 0 10px 0;
    margin: 0;
    font-size: 14px;
}

.breadcrumbs ol a {
    color: #fff;
    transition: 0.3s;
}

.breadcrumbs ol a:hover {
    text-decoration: underline;
}

.breadcrumbs ol li+li {
    padding-left: 10px;
}

.breadcrumbs ol li+li::before {
    display: inline-block;
    padding-right: 10px;
    color: #8894f6;
    content: "/";
}

